    import React,{useState,useEffect} from 'react';
    import {
    CBadge,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CTooltip,
    CRow,
    CButton,
    CPagination
    } from '@coreui/react';

    import { useHistory,useParams } from 'react-router-dom';

    import {
    useDispatch,
    useSelector
    } from 'react-redux'



    import {
    useFormFields,
    disableSubmitButton
    }  from '../../../helpers/hooksFormInput'
    import {
    cleanUpData,
    instantiateAbort
    } from '../../../helpers/componentHelperFunc'

    
    import Loader from 'react-loader-spinner'

    import { 
    ViewSingleUserDetailAction
    } from '../../../store/Actions/UserProfileAction';
import { TotalRevenuePerCelebrityAction } from '../../../store/Actions/UserEventAction';






    export default function UserProfile(props)  
    {


    const params = useParams();

    const {id} = params;

    const dispatch =  useDispatch();

    const singleUserProfile = useSelector(state => state.userProfile.singleUserProfile);
   const celebrityRevenue = useSelector(state=>state.eventReducer.totalRevenuePerCelebrity);

    const abortEffect = instantiateAbort();


    useEffect(() => {
    dispatch(ViewSingleUserDetailAction(id));
    dispatch(TotalRevenuePerCelebrityAction(id));
    return () => {
    cleanUpData(abortEffect);
    }
    }, []);


     




    return (
    <>
    <CRow >
    <CCol>
    <CCard>
    <CCardHeader>
    <b>User Details</b>
    </CCardHeader>
    <CCardBody>

    {
    singleUserProfile == 'loading' || singleUserProfile == '' ?

    <Loader type="ThreeDots" color="#321fdb" height={100} width={100} />
    :
    singleUserProfile.hasOwnProperty('data') && 
    singleUserProfile.success == true && singleUserProfile.data.data!=null?
    <>
    <div style={{textAlign:"center",marginTop:"30px",marginBottom:"30px"}}>
          <img src={
            singleUserProfile.data.data.profile_picture=="" ||
             singleUserProfile.data.data.profile_picture==null?
             require("../../../assets/images/default-avatar.png")
             :
            singleUserProfile.data.url+""+singleUserProfile.data.data.profile_picture
          }
style={{width:"150px",height:"150px"}}
/>
   </div>
     <div className="table-responsive">
    <table className="table table-striped table-hover " >
    
    {  
     <>
    
    
    
   
          
      <tbody>

            
            
          
     
    <tr key={Math.random()+""+Math.random()} >

    <td>Username : </td>
    <td><b>{singleUserProfile.data.data.username}</b></td>
    </tr>

    <tr key={Math.random()+""+Math.random()}>

    <td>Firstname : </td>
    <td><b>{singleUserProfile.data.data.firstname}</b></td>
    </tr>


    <tr key={Math.random()+""+Math.random()}>

    <td>Lastname : </td>
    <td><b>{singleUserProfile.data.data.lastname}</b></td>
    </tr>


    <tr key={Math.random()+""+Math.random()}>

<td>User Type : </td>
<td><b>{singleUserProfile.data.data.user_type}</b></td>
</tr>


<tr key={Math.random()+""+Math.random()}>

<td>Date of Birth : </td>
<td><b>{singleUserProfile.data.data.date_of_birth}</b></td>
</tr>


<tr key={Math.random()+""+Math.random()}>

<td>Email : </td>
<td><b>{singleUserProfile.data.data.email}</b></td>
</tr>

<tr key={Math.random()+""+Math.random()}>

<td>Mobile Number: </td>
<td><b>{singleUserProfile.data.data.mobile_no}</b></td>
</tr>

 {
   singleUserProfile.data.data.user_type=="celebrity"?
   celebrityRevenue.hasOwnProperty('success') && celebrityRevenue.success==true?
   <>
   <td>Total Profit Made: </td>
 <td><b>{"â‚¦"+parseInt(celebrityRevenue.data).toLocaleString()}</b></td>
   </>
   :
   null
   :
   null
 }

    </tbody>
    </>
    }


    
    </table>             
   </div>
    {
      singleUserProfile.data.data.social_media_account !="" && 
      singleUserProfile.data.data.social_media_account !=null?
      <>
       <CCardHeader>
      <b>Social Media Accounts</b>
      </CCardHeader>
        <CCardBody>
          <div className="table-responsive">
        <table className="table table-striped table-hover" >
       <tbody>
          
         {
           Object.entries(
             JSON.parse(singleUserProfile.data.data.social_media_account)
             ).map(([key,values])=>(
        
              <tr keys={Math.random()+""+Math.random()+""+Math.random()}>
              <td>{key}</td>
            <td><b>{values}</b></td>
                </tr>
            )
             )
          
         }  
         
         </tbody>
         </table>
         </div>
        </CCardBody>  
        </>
      :
      ""
    }


    </>
    :

   
    ""

    }


    </CCardBody>

   
 


    </CCard>
    </CCol>
    </CRow>
    </>
    );
    }
